<template>
  <div class="search">
    <van-nav-bar title="商品搜索" left-arrow @click-left="$router.go(-1)" />

    <van-search v-model="search" show-action placeholder="请输入搜索关键词" clearable>
      <template #action>
        <div @click="goSearch(search)">搜索</div>
      </template>
    </van-search>

    <!-- 搜索历史 -->
    <div class="search-history" v-if="history.length > 0">
      <div class="title">
        <span>最近搜索</span>
        <van-icon @click="clear" name="delete-o" size="16" />
      </div>
      <div class="list">
      <!-- 下面那行代码中，goSearch(item) 方法是一个点击事件处理函数，在模板中被绑定到历史记录列表的每个元素上。当某个历史记录被点击时，该方法会被触发，并传递对应的 item 作为参数。
            所以，在 div v-for="item in history" 的循环中，goSearch(item) 中的 item 指向当前循环迭代的历史记录项。也就是说，当点击某个历史记录项时，item 将指向所点击的那个历史记录的值。
            例如，当你点击 "白酒" 这个历史记录项时，goSearch(item) 中的 item 就会被替换为 "白酒" -->
        <div v-for="item in history" :key="item" class="list-item" @click="goSearch(item)">{{item}}</div>
        </div>
    </div>
  </div>
</template>

<script>
import { getHistoryList, setHistoryList } from '@/utils/storage'
export default {
  name: 'SearchIndex',
  data () {
    return {
      search: '', // 输入框的内容
      history: getHistoryList() // 历史记录
    }
  },
  methods: {
    goSearch (key) {
      // console.log('进行了搜索，搜索历史要更新')
      const index = this.history.indexOf(key)
      if (index !== -1) {
        // 看他们下标如果找到相同的就是等于-1了，没有找到那就是等于-1直接unshift就行了
        // 存在相同的项，将原有关键字移除
        // splice(从哪开始,删除几个)
        this.history.splice(index, 1)
      }
      this.history.unshift(key)

      setHistoryList(this.history)

      // 跳转到搜索页
      this.$router.push(`/searchlist?search=${key}`)
    },
    clear () {
      // 清空历史
      this.history = []
      setHistoryList([])
    }
  }
}
</script>

<style lang="less" scoped>
.search {
  .searchBtn {
    background-color: #fa2209;
    color: #fff;
  }
  ::v-deep .van-search__action {
    background-color: #c21401;
    color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
  }
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }
  .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
  }
  .list-item {
    width: 30%;
    text-align: center;
    padding: 7px;
    line-height: 15px;
    border-radius: 50px;
    background: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
}
</style>
