export default {
  // 此处编写的就是 VUe组件实例的 配置项, 通过一定语法，可以直接混入到组件内部
  // data methods computed 生命周期等...都可以放到这里来
  // 注意点:如果此处和组件内，提供了同名的data或methods，则组件内优先级高
  // 如果编写了生命周期函数，则mixins中的生命周期函数和页面的生命周期函数，会用到数组管理，然后统一执行
  // 根据登录状态，判断是否需要登录确认框
  // 1.如果未登录=>显示确认框 返回true
  // 2.如果已登录=>啥也不干   返回false

  methods: {

    loginConfirm () {
    // 判断 token是否存在
      if (!this.$store.getters.token) {
        this.$dialog.confirm(
          {
            title: '温馨提示',
            message: '需要登录才能继续操作',
            confirmButtonText: '去登录',
            cancelButtonText: '再逛逛'
          }).then(() => {
          this.$router.replace({
            path: '/login',
            query: {
              backUrl: this.$route.fullPath
            }
          })
        }).catch(() => {})
        return true
      }
      return false

    // console.log(res)
    }
  }
}
